.social-block__list li {
  flex: none;
  margin-bottom: 0.5rem;
  opacity: 0;
}
@media (min-width: 2000px) {
  .social-block__list li {
    margin-bottom: 5px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .social-block__list li {
    margin: 0 1.25rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__list li {
    margin: 0 1.25rem;
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: #ffffff;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 2000px) {
  .social-block__link {
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .social-block__link {
    color: #ffffff;
    background-color: #5f458c;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__link {
    color: #ffffff;
    background-color: #5f458c;
  }
}
.social-block__link:hover {
  color: #A67EE5;
}
.social-block__link:focus {
  color: #A67EE5;
  background-color: #ffffff;
  border-color: #A67EE5;
  outline: none;
}
.social-block__link:active, .social-block__link:active:focus {
  color: #A67EE5;
  background-color: #ffffff;
  border-color: #ffffff;
}
.social-block__link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.social-block__link--fb svg {
  width: 0.8rem;
  height: 1.7rem;
}
@media (min-width: 2000px) {
  .social-block__link--fb svg {
    width: 8px;
    height: 17px;
  }
}
.social-block__link--insta svg {
  width: 1.5rem;
  height: 1.5rem;
}
@media (min-width: 2000px) {
  .social-block__link--insta svg {
    width: 15px;
    height: 15px;
  }
}
.social-block__link--vk svg {
  width: 1.7rem;
  height: 1rem;
}
@media (min-width: 2000px) {
  .social-block__link--vk svg {
    width: 10px;
    height: 10px;
  }
}